<template>
	<div class="services">
		<Detail />

		<div class="server-meal" v-if="$route.query.type == 'experience'">
			<div class="server-meal-title">{{language.taipu_services_examination}}</div>

			<div v-for="item in experience">
				<div class="hospital-name">{{item.hospital}}</div>
				<div class="ul">
					<router-link tag="div" v-for="data in item.data" :to="{ name: 'meal-detail', query: { id: data.id } }">{{data.title}}</router-link>
				</div>
			</div>
		</div>

		<div class="server-meal" v-if="$route.query.type == 'cosmetology'">
			<div class="server-meal-title">{{language.taipu_services_regenerate}}</div>

			<div class="hospital" v-for="item in regenerate">
				<div class="hospital-name">{{item.hospital}}</div>

				<div class="hospital-meal" v-for="(data, index) in item.data">
					<div>【 {{data.title}} 】</div>
					<div class="ul" v-for="link in data.data">
						<router-link tag="div" :to="{ name: 'mdetail', query: { id: link.id } }">{{link.title}}</router-link>
					</div>
				</div>
			</div>
		</div>

		<div class="server-meal" v-if="$route.query.type == 'regenerate'">
			<div class="server-meal-title">{{language.taipu_services_regenerate}}</div>

			<div v-for="item in cosmetology">
				<div class="hospital-name">{{item.hospital}}</div>
				<div class="ul">
					<router-link tag="div" v-for="data in item.data" :to="{ name: 'mdetail', query: { id: data.id } }">{{data.title}}</router-link>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	import Detail from './Detail.vue'

	const titles = {
		44: window.language.anti_aging_care_beauty + '（'+ window.language.beauty_medical_equipment +'）',
		48: window.language.injection_therapy
	}

	const setAry = (data, status) => {
		let _item = [];
		let ready = false;
		let _data = [];

		data.meal.map(item => {
			if (item.status == status) {
				_item.push(item);
				ready = true;
			}
		})

		if (ready) {
			var list = {
				hospital: data.hospital,
				data: _item
			}
		}

		return list;
	}

	export default {
		components: { Detail },
		data() {
			return {
				language: {},
				experience: [],
				regenerate: [],
				cosmetology: []
			}
		},
		created() {
			this.language = window.language;
			fnServerMeal(this);
		},
		mounted() {
			this.$emit('handlerTabbar', false);
		}
	}

	function fnServerMeal(that, params) {
		that.$request.serverMeal(params, response => {
			if (response.data.code === 1) {
				let result = response.data.data;
				that.serverMeal = result.list;

				result.list.map(list => {
					// 日本高端体验
					if (that.$route.query.type == 'experience') {
						setAry(list, 1) && that.experience.push(setAry(list, 1));
					}

					// 医疗美容
					if (that.$route.query.type == 'cosmetology') {
						let _item = [];
						let ready = false;
						for (var name in titles) {
							_item.push({
								title: titles[name],
								data: []
							})
						}

						let _data = [];
						list.meal.map(item => {
							if (titles[list.id + '' + item.status]) {
								_data.push({
									title: titles[list.id + '' + item.status],
									item: item
								});

								ready = true;
							}
						})

						if (ready) {
							_data.map(list => {
								_item.map(item => {
									if (list.title == item.title) {
										item.data.push(list.item);
									}
								})
							})

							let _list = {
								hospital: list.hospital,
								data: _item
							}

							that.regenerate.push(_list)
						}
					}

					// 再生医疗
					if (that.$route.query.type == 'regenerate') {
						setAry(list, 2) && that.cosmetology.push(setAry(list, 2));
					}
				})
			}
		})
	}
</script>

<style lang="scss">
	@import './../../styles/detail/service.scss';
</style>